import { Fragment } from "react";
import { Route } from "react-router";
import Footer from "./Layout/Footer/Footer";
import Header from "./Layout/Header/Header";
import HomeCarousel from "./Layout/HomeCarousel/HomeCarousel";



export const HomeTemplate = (props) => { //path,exact,component
    const { Component, ...propsRoute } = props;

    return <Route {...propsRoute} render={(propsRoute) => {//props.location,props.history,props.match
        return <div style={{ backgroundColor: "#181616 " }}>
            <Header />
            <Component {...propsRoute} />
            <Footer />

        </div>;
    }} />;
};