import React, { Fragment, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { datGheAction, datVeAction, layChiTietPhongVeAction } from '../../redux/actions/QuanLyDatVeAction';
import { CloseOutlined, UserOutlined, CheckOutlined, AppleOutlined, AndroidOutlined, HomeOutlined, DollarCircleOutlined, HistoryOutlined } from '@ant-design/icons';
import { Tabs, Pagination, Button, Divider, Checkbox } from 'antd';
import style from './Checkout.module.css';
import './Checkout.css';
import _ from 'lodash';
import { useTranslation } from 'react-i18next';
import { CHUYEN_TAB, DAT_GHE } from '../../redux/types/QuanLyDatVeType';
import { thongTinTaiKhoanAction } from '../../redux/actions/QuanLyNguoiDungAction';
import moment from 'moment';
import { connection } from '../../index';
import { history } from '../../App';
import { TOKEN, USER_LOGIN } from '../../util/settings/config';
import { NavLink } from 'react-router-dom';
import Home from '../Home/Home';
import Swal from 'sweetalert2';
function Checkout(props) {
    const { userLogin, thongTinTaiKhoan } = useSelector(state => state.QuanLyNguoiDungReducer);
    const { t, i18n } = useTranslation();

    const { chiTietPhongVe, danhSachGheDangDat, danhSachGheKhachDat } = useSelector(state => state.QuanLyDatVeReducer);
    const { thongTinPhim, danhSachGhe } = chiTietPhongVe;
    const dispatch = useDispatch();


    const renderSeats = () => {
        return danhSachGhe?.map((ghe, index) => {
            let classGheVip = ghe.loaiGhe === 'Vip' ? 'gheVip' : '';
            let classGheDaDat = ghe.daDat === true ? 'gheDaDat' : '';
            let classGheDangDat = '';
            let indexGheDD = danhSachGheDangDat.findIndex(gheDD => gheDD.maGhe === ghe.maGhe);
            if (indexGheDD != -1) {
                classGheDaDat = 'gheDangDat';
            }
            let classGheCuaMinh = '';
            if (userLogin.taiKhoan === ghe.taiKhoanNguoiDat) {
                classGheCuaMinh = 'gheCuaMinh';
            }
            //Kiểm tra ghế có được người khác đang đặt ko
            let classGheKhachDat = '';
            let indexGheKhachDat = danhSachGheKhachDat.findIndex(gheKD => gheKD.maGhe === ghe.maGhe);
            if (indexGheKhachDat != -1) {
                classGheKhachDat = 'gheKhachDat';
            }
            return <Fragment key={index}>

                <button disabled={ghe.daDat | classGheKhachDat != ''} className={`ghe font-semibold ${classGheVip} ${classGheDaDat} ${classGheDangDat}${classGheCuaMinh} ${classGheKhachDat}`} key={index} onClick={() => {
                    dispatch(datGheAction(ghe, props.match.params.id));
                }}>
                    {ghe.taiKhoanNguoiDat === userLogin.taiKhoan ? <UserOutlined /> : ghe.daDat ? <CloseOutlined /> : ghe.stt}
                </button>

            </Fragment>;
        });
    };
    useEffect(() => {
        dispatch(thongTinTaiKhoanAction());
        dispatch(layChiTietPhongVeAction(props.match.params.id));

        //vừa vào trang load tất cả ghế của các người khcas đnag đặt
        connection.invoke('loadDanhSachGhe', props.match.params.id);
        //Có 1 client nào đó thực hiện đặt vé thành công mình sẽ load lại danh sách phòng vé của lịch chiếu đó
        connection.on('datVeThanhCong', () => {
            dispatch(layChiTietPhongVeAction(props.match.params.id));
        });

        //loading dánh sách ghế đang đặt từ server về
        connection.on("loadDanhSachGheDaDat", (dsGheKhachDat) => {
            dsGheKhachDat = dsGheKhachDat.filter(item => item.taiKhoan !== userLogin.taiKhoan);
            // console.log("dsGheKhachDat Filter", dsGheKhachDat);
            //Bước 2 dùng reduce vì reduce ko làm vs những cái ko có giá trị
            let arrGheKhachDat = dsGheKhachDat.reduce((result, item, index) => {
                let arrGhe = JSON.parse(item.danhSachGhe);
                return [ ...result, ...arrGhe ];
            }, []);
            // console.log("arrGheKhachDat reduce", arrGheKhachDat);
            arrGheKhachDat = _.uniqBy(arrGheKhachDat, 'maGhe');
            dispatch({
                type: 'GHE_KHACH_DAT',
                arrGheKhachDat
            });
            //load lai trang
            window.addEventListener("beforeunload", clearGhe);

            //component unmount ( khi bam qua trang khac)
            return () => {
                clearGhe();
                window.removeEventListener('beforeunload', clearGhe);
            };

        });
    }, []);
    const clearGhe = function (event) {
        connection.invoke('huyDat', userLogin.taiKhoan, props.match.params.id);
    };
    return (
        <div className='min-h-screen mt-5 '>
            <div className='grid grid-cols-12'>
                <div className='md:col-span-9 col-span-12'>
                    <div className='flex flex-col items-center mt-5'>
                        <div className='bg-black' style={{ width: '80%', height: 15 }}>

                        </div>
                        <div className={`${style[ 'trapezoid' ]} text-center`}>
                            <h1 className='text-gray-900 mt-3' >{t('checkout.manhinh')}</h1>
                        </div>
                        <div className='grid md:grid-cols-12 grid-cols-6'>
                            {renderSeats()}
                        </div>
                        <div className='mt-5 flex justify-center w-full'>
                            <table className="min-w-full divide-y divide-gray-200">
                                <thead className="bg-gray-50 p-5">
                                    <tr>
                                        <th>{t('checkout.ghechuadat')}</th>
                                        <th>{t('checkout.ghedangdat')}</th>
                                        <th>{t('checkout.ghevip')}</th>
                                        <th>{t('checkout.ghedaduocdat')}</th>
                                        <th>{t('checkout.ghecuabandat')}</th>
                                        <th>{t('checkout.ghenguoikhacdat')}</th>
                                    </tr>
                                </thead>
                                <tbody className="bg-white divide-y divide-gray-200">
                                    <tr>
                                        <td className='text-center'>
                                            <button className='ghe'><CheckOutlined /></button>
                                        </td>
                                        <td className='text-center'>
                                            <button className='ghe gheDangDat'><CheckOutlined /></button>
                                        </td>
                                        <td className='text-center'>
                                            <button className='ghe gheVip'><CheckOutlined /></button>
                                        </td>
                                        <td className='text-center'>
                                            <button className='ghe gheDaDat'> <CloseOutlined /></button>
                                        </td>
                                        <td className='text-center'>
                                            <button className='ghe gheCuaMinh'> <UserOutlined /></button>
                                        </td>
                                        <td className='text-center'>
                                            <button className='ghe gheKhachDat'> <CheckOutlined /></button>
                                        </td>

                                    </tr>
                                </tbody>
                            </table>


                        </div>
                    </div>
                </div>
                <div className='md:col-span-3 col-span-12 font-semibold '>
                    <h3 className='text-green-400 text-center text-2xl'> {danhSachGheDangDat.reduce((tongTien, ghe, index) => {
                        return tongTien += ghe.giaVe;
                    }, 0).toLocaleString()} VND</h3>
                    <hr />
                    <h3 className='text-xl mt-2'>{thongTinPhim?.tenPhim}</h3>
                    <p>{t('checkout.diadiem')}: {thongTinPhim?.diaChi}</p>
                    <p>{t('detail.ngaykhoichieu')}: {thongTinPhim?.ngayChieu} - {thongTinPhim?.gioChieu} {thongTinPhim?.tenRap}</p>
                    <hr />
                    <div className='grid grid-cols-2 my-5'>
                        <div className='col-span-2'>
                            <span className='text-red-400 text-lg mr-2'>{t('checkout.ghe')}:</span>

                        </div>
                        <div className='col-span-2'>
                            {_.sortBy(danhSachGheDangDat, [ 'stt' ]).map((gheDD, index) => {
                                return <span key={index} className='text-green-500 text-xl ml-2'>
                                    {gheDD.stt}
                                </span>;
                            })}
                        </div>
                        <div className='col-span-1 text-right text-green-400 text-lg'>
                            {danhSachGheDangDat.reduce((tongTien, ghe, index) => {
                                return tongTien += ghe.giaVe;
                            }, 0).toLocaleString()} VND
                        </div>
                    </div>
                    <hr />
                    <div className='my-5'>
                        <i>{t('profile.email')}</i> <br />
                        {userLogin.email}
                    </div>
                    <hr />
                    <div className='my-5'>
                        <i>{t('profile.sodienthoai')}</i> <br />
                        {thongTinTaiKhoan?.soDT}
                    </div>
                    <hr />
                    <div className='' style={{ marginBottom: 0 }}>
                        <div className='bg-green-500 text-white w-full text-center py-3 font-bold text-2xl cursor-pointer' onClick={() => {
                            let thongtinDatVe = {
                                maLichChieu: props.match.params.id,
                                danhSachVe: danhSachGheDangDat
                            };
                            Swal.fire({
                                icon: 'question',
                                title: 'Đặt vé!',
                                text: `Bạn có muốn đặt vé phim này không!`,
                                showCancelButton: true,
                                confirmButtonText: 'Đặt',
                                cancelButtonText: 'Không'
                            }).then((info) => {
                                if (info.isConfirmed) {
                                    dispatch(datVeAction(thongtinDatVe));
                                }

                            });


                        }}>
                            {t('booking')}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
function Ketqua(props) {
    const { t, i18n } = useTranslation();
    const dispatch = useDispatch();
    const { thongTinTaiKhoan, userLogin } = useSelector(state => state.QuanLyNguoiDungReducer);
    useEffect(() => {
        dispatch(thongTinTaiKhoanAction());
    }, []);
    const renderLichSuDatVe = () => {
        return thongTinTaiKhoan.thongTinDatVe?.map((ve, index) => {
            return <div className="p-2 lg:w-1/3 md:w-1/2 w-full" key={index}>
                <div className="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                    <img alt="team" className="w-16 h-16 bg-gray-100 object-cover object-center flex-shrink-0 rounded-full mr-4" src={ve.hinhAnh} onError={(e) => {
                        e.target.src = '/images/movies/end-game.jpg';
                    }} />
                    <div className="flex-grow">
                        <h2 className="text-yellow-800 title-font font-medium text-xl">{ve.tenPhim}</h2>
                        <p>{t('detail.ngaykhoichieu')}: {moment(ve.ngayDat).format('hh:mm A - DD-MM-YYYY')}</p>
                        <p className="text-gray-500 mb-0">{t('result.mave')}: {ve.maVe}</p>
                        <p className="text-gray-500">{t('result.giave')}: {ve.giaVe}</p>
                        <p>{t('result.tenhethongrap')}: {_.first(ve.danhSachGhe).tenHeThongRap}</p>
                        <p>{t('result.tencumrap')}: {_.first(ve.danhSachGhe).tenCumRap}</p>
                        <p>{t('result.tenghe')}: {ve.danhSachGhe.map((ghe, index) => {
                            return <Fragment>
                                <span className='ml-2 text-red-600'>{ghe.tenGhe}</span>
                                {(index + 1) % 7 === 0 ? <br /> : ''}
                            </Fragment>;
                        })}</p>
                    </div>
                </div>
            </div>;
        });
    };
    return <div>
        <section className="text-gray-600 body-font result-ticket">
            <div className="container px-5 py-24 mx-auto">
                <div className="flex flex-col text-center w-full mb-20">
                    <h1 className="sm:text-3xl text-2xl font-medium title-font mb-4 text-purple-600">{t('result.lichsu')} </h1>
                    <p>{t('profile.sodienthoai')} : <span className='text-green-500'>{userLogin.hoTen}</span></p>
                    <p>{t('profile.email')} : <span className='text-green-500'>{userLogin.email}</span></p>
                    <p>{t('profile.sodienthoai')} : <span className='text-green-500'>{userLogin.soDT}</span></p>
                    <p className="lg:w-2/3 mx-auto leading-relaxed text-base">{t('result.good')}</p>
                </div>
                <div className="flex flex-wrap -m-2">
                    {renderLichSuDatVe()}

                </div>
            </div>
        </section>;
    </div>;

}


const { TabPane } = Tabs;
export default function (props) {
    const { t, i18n } = useTranslation();
    const { tabActive } = useSelector(state => state.QuanLyDatVeReducer);
    const { userLogin } = useSelector(state => state.QuanLyNguoiDungReducer);
    const dispatch = useDispatch();
    function callback(key) {
        dispatch({
            type: "CHANGE_TAB",
            key
        });
    }
    useEffect(() => {

        return () => {
            dispatch({
                type: "CHANGE_TAB",
                key: '1'
            });
        };
    }, []);
    const operations = <Fragment>

        {!_.isEmpty(userLogin) ? <Fragment> <button onClick={() => {
            history.push('/profile');
        }}> <div style={{ width: 50, height: 50, display: 'flex', justifyContent: 'center', alignItems: 'center' }} className="text-2xl ml-5 rounded-full bg-red-200">{userLogin.taiKhoan.substr(0, 1)}</div>Hello ! {userLogin.taiKhoan}</button> <button onClick={() => {
            localStorage.removeItem(USER_LOGIN);
            localStorage.removeItem(TOKEN);
            history.push('/home');
            window.location.reload();
        }} className="text-blue-800">{t('profile.dangxuat')}</button> </Fragment> : ''}

    </Fragment>;
    return <div className='p-5'>
        <Tabs tabBarExtraContent={operations} activeKey={tabActive} onChange={callback}>
            <TabPane

                tab={
                    <span>
                        <DollarCircleOutlined />
                        01 {t('checkout.menu1')}
                    </span>
                }
                key="1"
            >
                <Checkout {...props} />
            </TabPane>
            <TabPane

                tab={
                    <span>
                        <HistoryOutlined />
                        02 {t('checkout.menu2')}
                    </span>
                }
                key="2"
            >
                <Ketqua {...props} />
            </TabPane>
            <TabPane
                tab={
                    <span onClick={() => {
                        history.push('/');
                    }}>
                        <HomeOutlined />
                        {t('home')}
                    </span>
                }

            >

            </TabPane>
        </Tabs>
    </div>;
}

