import { SET_CAROUSEL } from "../types/CarouselType";

const initialState = {
    listBanner: [
        {
            "maBanner": 1,
            "maPhim": 1282,
            "hinhAnh": "http://movieapi.cyberlearn.vn/hinhanh/ban-tay-diet-quy.png"
        }
    ]
};

export default (state = initialState, action) => {
    switch (action.type) {
        case SET_CAROUSEL: {
            return { ...state, listBanner: action.listBanner };
        }
        default:
            return state;
    }
};
