import './App.css';
import './grid.css';
import { createBrowserHistory } from 'history';
import { Route, Router, Switch } from 'react-router';
import { HomeTemplate } from './templates/HomeTemplate/HomeTemplate';
import Home from './pages/Home/Home';
import Login from './pages/Login/Login';
import Register from './pages/Register/Register';
import DetailsBooking from './pages/DetailsBooking/DetailsBooking';
import Checkout from './pages/Checkout/Checkout';
import { Suspense, lazy } from 'react';
import CheckoutTemplate from './templates/CheckoutTemplate/CheckoutTemplate';
import { UserTemplate } from './templates/UserTemplate/UserTemplate';
import Loading from './components/Loading/Loading';
import Profile from './pages/Profile/Profile';
import AdminTemplate from './templates/AdminTemplate/AdminTemplate';
import Dashboard from './pages/Admin/Dashboard/Dashboard';
import Films from './pages/Admin/Films/Films';
import Showtime from './pages/Admin/Showtime/Showtime';
import AddFilms from './pages/Admin/Films/AddFilms';
import LoginAdmin from './pages/LoginAdmin/LoginAdmin';
import EditFilm from './pages/Admin/EditFilms/EditFilm';
import EditUser from './pages/Admin/Dashboard/EditUser';
import VideoModal from './components/ModalVideo/VideoModal';

export const history = createBrowserHistory();
function App() {
  return (
    <Router history={history}>

      <Loading />
      <VideoModal />
      <Switch>
        <HomeTemplate path='/' exact Component={Home}></HomeTemplate>
        <HomeTemplate path='/home' exact Component={Home}></HomeTemplate>
        <HomeTemplate path='/details/:id' exact Component={DetailsBooking}></HomeTemplate>
        <HomeTemplate path='/profile' exact Component={Profile}></HomeTemplate>
        <CheckoutTemplate path="/checkout/:id" exact Component={Checkout} />
        <UserTemplate path="/login" exact Component={Login} />
        <UserTemplate path="/register" exact Component={Register} />
        <Route path='/admin' exact component={LoginAdmin} />
        <Route path='/login' exact component={Login} />
        <Route path='/register' exact component={Register} />
        <AdminTemplate path="/mainAdmin" exact Component={Dashboard} />
        <AdminTemplate path="/mainAdmin/films" exact Component={Films} />
        <AdminTemplate path="/mainAdmin/films/edit/:id" exact Component={EditFilm} />
        <AdminTemplate path="/mainAdmin/showtimes/:id/:tenphim" exact Component={Showtime} />
        <AdminTemplate path="/mainAdmin/users" exact Component={Dashboard} />
        <AdminTemplate path="/mainAdmin/users/edituser/:user" exact Component={EditUser} />
        <AdminTemplate path="/mainAdmin/addfilms" exact Component={AddFilms} />
      </Switch>
    </Router>
  );
}

export default App;
